export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar
		};
	},
	name: 'cu-custom',
	computed: {
		style() {
			var StatusBar= this.StatusBar;
			var CustomBar= this.CustomBar;
			var bgImage = this.bgImage;
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`;
			if (this.bgImage) {
				style = `${style}background-image:url(${bgImage});`;
			}
			return style
		}
	},
	props: {
		bgColor: {
			type: String,
			default: ''
		},
		isBack: {
			type: [Boolean, String],
			default: false
		},
		bgImage: {
			type: String,
			default: ''
		},
		tourl:{
			type: String,
			default: ''
		}
	},
	methods: {
		BackPage() {
			if(this.tourl==''||this.tourl==null)
			{
				console.log(11);
				uni.navigateBack({
				delta: 1
			});
			}
			else
			{
				console.log(1);
				uni.redirectTo({
					url:this.tourl
				})
			}
			
		}
	}
}
